import { h } from '@stencil/core';
import { newSpecPage } from '@stencil/core/testing';
import { RwcNavigation } from '../navigation';

const containerExpectedClassesDefault = ['container', 'horizontal'];

describe('rwc-navigation', () => {
  it('should render the navigation', async () => {
    const page = await newSpecPage({
      components: [RwcNavigation],
      template: () => (
        <rwc-navigation></rwc-navigation>
      ),
    });

    expect(page.root).toEqualHtml(`
      <rwc-navigation type="horizontal">
        <mock:shadow-root>
          <div class="${ containerExpectedClassesDefault.join(' ') }">
            <slot name="before"></slot>
            <slot></slot>
            <slot name="after"></slot>
          </div>
        </mock:shadow-root>
      </rwc-navigation>
    `);
  });
});
